import React, { Suspense, lazy } from "react"
import { Router, Switch, Route } from "react-router-dom"
import { history } from "./history"
import { connect } from "react-redux"
// import { Redirect } from "react-router-dom"
import Spinner from "./components/@vuexy/spinner/Loading-spinner"
import { ContextLayout } from "./utility/context/Layout"

const error404 = lazy(() => import("./views/misc/error/404"))
const register = lazy(() => import("./views/auth/Register"))
const Login = lazy(() => import("./views/auth/Login"))
const Profile = lazy(() => import("./views/auth/Profile"))
const forgotPassword = lazy(() =>
  import("./views/auth/ForgotPassword")
)

// deals management start
const Deals = lazy(() => import("./views/deal_management/deals"))
const NewDeal = lazy(() => import("./views/deal_management/newDeal"))
// deals management end

// admin
const Users = lazy(() => import("./views/admin/users"))
const Company = lazy(() => import("./views/admin/company"))
const CompanySettings = lazy(() => import("./views/admin/company_settings"))
const DeskManager = lazy(() => import("./views/admin/desk_manager"))
const FinancePeople = lazy(() => import("./views/admin/finance_people"))
const FlooringCompanies = lazy(() => import("./views/admin/flooring_companies"))
const GapProviders = lazy(() => import("./views/admin/gap_providers"))
const LendingSources = lazy(() => import("./views/admin/lending_sources"))
const SalesPeople = lazy(() => import("./views/admin/sales_people"))
const WarrantyProviders = lazy(() => import("./views/admin/warranty_providers"))

// reports start
const CompanyGross = lazy(() => import("./views/reports/company_gross"))
const R_DeskManager = lazy(() => import("./views/reports/desk_manager"))
const R_SalesPeople = lazy(() => import("./views/reports/sales_people"))
const Finance = lazy(() => import("./views/reports/finance"))
const Lenders = lazy(() => import("./views/reports/lenders"))
const Flooring = lazy(() => import("./views/reports/flooring"))
const Funded = lazy(() => import("./views/reports/funded"))
const SalesBoard = lazy(() => import("./views/reports/sales_board"))
const GrossVehicle = lazy(() => import("./views/reports/gross_w_vehicle"))
const LeadSources = lazy(() => import("./views/reports/lead_sources"))
const FinanceApproval = lazy(() => import("./views/reports/finance_approval"))
const TimeToFund = lazy(() => import("./views/reports/time_to_fund"))
const FinancePenetration = lazy(() => import("./views/reports/finance_penetration"))
const GrossNewCars = lazy(() => import("./views/reports/gross_new_cars"))
const GrossUsedCars = lazy(() => import("./views/reports/gross_used_cars"))
const TradeIn = lazy(() => import("./views/reports/trade_in"))
// reports end

const RouteConfig = ({ component: Component, fullLayout, ...rest }) => (
  <Route
    {...rest}
    render={props => {
      return (
        <ContextLayout.Consumer>
          {context => {
            let LayoutTag =
              fullLayout === true
                ? context.fullLayout
                : context.state.activeLayout === "horizontal"
                ? context.horizontalLayout
                : context.VerticalLayout
            return (
              <LayoutTag {...props} permission={props.user}>
                <Suspense fallback={<Spinner />}>
                  <Component {...props} />
                </Suspense>
              </LayoutTag>
            )
          }}
        </ContextLayout.Consumer>
      )
    }}
  />
)
const mapStateToProps = state => {
  return {
    user: state.auth.login.userRole
  }
}

const AppRoute = connect(mapStateToProps)(RouteConfig)

class AppRouter extends React.Component {
  render() {
    return (
      // Set the directory path if you are deploying in sub-folder
      <Router history={history}>
        <Switch>
          <AppRoute path="/" exact component={Login}  fullLayout/>
          <AppRoute path="/register" exact component={register} fullLayout/>
          <AppRoute path="/misc/error/404" component={error404} fullLayout />
          <AppRoute path="/login" component={Login} fullLayout />
          <AppRoute path="/forgot-password" component={forgotPassword} fullLayout />
          <AppRoute path="/pages/profile" component={Profile} />
          {/* deals management  start */}
          <AppRoute path="/deal_management/deals/:type" component={Deals} />
          <AppRoute path="/newDeal" component={NewDeal} />
          {/* admin  start */}
          <AppRoute path="/admin/users" component={Users} />
          <AppRoute path="/admin/company" component={Company} />
          <AppRoute path="/admin/company_settings" component={CompanySettings} />
          <AppRoute path="/admin/desk_manager" component={DeskManager} />
          <AppRoute path="/admin/finance_people" component={FinancePeople} />
          <AppRoute path="/admin/flooring_companies" component={FlooringCompanies} />
          <AppRoute path="/admin/gap_providers" component={GapProviders} />
          <AppRoute path="/admin/lending_sources" component={LendingSources} />
          <AppRoute path="/admin/sales_people" component={SalesPeople} />
          <AppRoute path="/admin/warranty_providers" component={WarrantyProviders} />
          <AppRoute path="/admin/company_gross" component={CompanyGross} />
          {/* reports start */}
          <AppRoute path="/reports/company_gross" component={CompanyGross} />
          <AppRoute path="/reports/desk_manager" component={R_DeskManager} />
          <AppRoute path="/reports/sales_people" component={R_SalesPeople} />
          <AppRoute path="/reports/finance" component={Finance} />
          <AppRoute path="/reports/lenders" component={Lenders} />
          <AppRoute path="/reports/flooring" component={Flooring} />
          <AppRoute path="/reports/funded" component={Funded} />
          <AppRoute path="/reports/sales_board" component={SalesBoard} />
          <AppRoute path="/reports/gross_w_vehicle" component={GrossVehicle} />
          <AppRoute path="/reports/lead_sources" component={LeadSources} />
          <AppRoute path="/reports/finance_approval" component={FinanceApproval} />
          <AppRoute path="/reports/time_to_fund" component={TimeToFund} />
          <AppRoute path="/reports/finance_penetration" component={FinancePenetration} />
          <AppRoute path="/reports/gross_new_cars" component={GrossNewCars} />
          <AppRoute path="/reports/gross_used_cars" component={GrossUsedCars} />
          <AppRoute path="/reports/trade_in" component={TradeIn} />
          {/* reports end */}
          <AppRoute component={error404} fullLayout />
        </Switch>
      </Router>
    )
  }
}

export default AppRouter
